.site-list{

}

.site-list__item{
  text-align: center;

}

.site-list__link{
display: block;
width: 100%;
  color:$white;
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
 text-transform: uppercase;
 border-top: 1px solid #475360;
 border-bottom: 1px solid #000001;
 padding: 25px 70px;
}

.site-list__link a[href]{
  &:hover{}
  &:active{}
}

@media (min-width:$desktop-width){
.site-list__link{
  padding: 0;
  border:none;
 font-size: 18px;
 line-height: 18px;
 font-weight: 400;
 text-transform: none;
}
}

