.table-price{
  padding-top: 90px;
  margin-bottom: 50px;
  background: #f2f2f2 url("/img/bg-triangle-white.svg") no-repeat center top;
background-size: 100% 51px;
width: 100%;
height: 100%;

}
.table-price__list{
  margin: 0 auto;
  margin-bottom: 40px;

}
.table-price__body{
  background-color: #fff;
}
.table-price__value{
  border: 2px solid #e5e5e5;
  background-color: $font-color;
  color:$white;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  &--empty{
    background-color: #f2f2f2;
border: none;
border-bottom: 2px solid #e5e5e5;}
&--hidden{
  display: none;
}
}

.table-price__rates{
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;

  }
  .table-price__toggles{
    text-align: center;
    padding-bottom: 50px;
  }

.table-price__item{

background-color: #fff;
border: 2px solid #e5e5e5;
border-top-width: 2px;
border-top-width: 0;

font-size: 14px;
font-weight: 400;
background-color: #fff;
color: #283645;
&--hidden{
  display: none;
}
}

.table-price__feature-status {
  background: url('/img/icon-yes.svg') no-repeat center;
background-size: 30px;
background-position: 93% 50%;
display: block;

}


